import React from "react";
import { useHistory } from 'react-router-dom'
import style from './ES.module.css'

// import Article from './articles/articles.js'
// import E1 from './Text/e1.js'
function Article(props){
  let history = useHistory()
  // let { path, url } = useRouteMatch();
  function jumpto(){
    history.push(`${props.path}`)
  }
  return (
    <div className={style.items} onClick={jumpto}>
      <div className={style.content}>
        {props.content}
      </div>
      <div className={style.title}>
        {props.title}
      </div>
    </div>
  )
}

function F1(props){
  let articles = props.articles
  let articlesNode = articles.map((item, index)=>
    // <div key={index} className={style.items}>{str}</div>
    <Article key={index} title={item.title} path={item.path}></Article>
  )
  return articlesNode
}

function ES(){
  let articles = [
    {title:'关于此站', path:'/ES/AboutThisSite'},
    {title:'同源策略', path:'/ES/SameOriginPolicy'},
    {title:'1em有多大', path:'/ES/HowBigis1Em'},
    {title:'防抖与节流', path:'/ES/debounceAndThrottle'},
    {title:'冒泡与捕获', path:'/ES/Events'}
  ]
  return (
    <div>
      <div className={style.App}>
        <F1 articles={articles}></F1>
      </div>
    </div>
  )
}
export default ES